.modal {
	--transition-time: 0.3s;
	position: fixed;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	z-index: 1000;
	cursor: pointer;
	overflow-y: auto;
	overflow-x: hidden;
	text-align: center;
	opacity: 0;
	visibility: hidden;
	transition: opacity var(--transition-time), visibility var(--transition-time);
}

.modal__wrapp {
	display: none;
	cursor: default;
    width: fit-content;
    height: fit-content;
}


.modal.is-open {
	opacity: 1;
	visibility: visible;
	transition: opacity var(--transition-time), visibility var(--transition-time);
}

.modal__wrapp.modal-open {
	display: flex;
}


.disable-scroll {
	position: relative;
	overflow: hidden;
	height: 100vh;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
}

.fade {
	opacity: 0;
	transition: opacity var(--transition-time);
}

.fade.animate-open {
	opacity: 1;
	transition: opacity var(--transition-time);
}

.fadeInUp {
	opacity: 0;
	transform: translateY(vw(-100));
	transition: opacity var(--transition-time), transform var(--transition-time);
}

.fadeInUp.animate-open {
	opacity: 1;
	transform: translateY(0);
	transition: opacity var(--transition-time), transform var(--transition-time);
}

.modal__content{
    position: absolute;
    bottom: vw(50);
    right: vw(85);

    .order__call{
        z-index: 9999;
        background: rgba(255, 255, 255, 0.08);
        backdrop-filter: blur(vw(67.6));
    }
}

p.form__text{
    font-family: $MS-Regular;
    font-size: vw(16);
    color: rgba($color: #fff, $alpha: 0.68);
    padding-top: vw(10);
    text-align: center;
    line-height: 120%;
}

.modal__close{
	display: flex;
	width: vmin(30);
	height: vmin(30);
	border-radius: 50%;
	background-color: #00DCE1;
	position: absolute;
	right: vmin(40);
	bottom: vmin(415);
	z-index: 99999;
    border: vmin(2) #AFFDFF solid;
    justify-content: center;
    align-items: center;

    span{
        position: relative;
        width: vmin(30);
        height: vmin(30);
        border-radius: 50%;
        &::after{
            content: '';
            position: absolute;
            top: vmin(13);
            left: vmin(5.5);
            transform: rotate(45deg);
            width: vmin(15);
            height: vmin(2);
            background-color: #AFFDFF;
            border-radius: vmin(5);
        }

        &::before{
            content: '';
            position: absolute;
            top: vmin(13);
            left: vmin(5.5);
            transform: rotate(135deg);
            width: vmin(15);
            height: vmin(2);
            border-radius: vmin(5);
            background-color: #AFFDFF;
        }
    }
}

#quiz__modal{
	.modal__content{
		bottom: auto;
		top: 50%;
		right: auto;
		left: 50%;
		transform: translate(-50%, -50%);
	}
}

@media (min-width: 990px){
	.modal__close{
		display: none;
	}
}

@media (max-width: 990px){
	.modal__content{
		bottom: vmin(30);
		right: vmin(42);

		.order__call{
			z-index: 9999;
			background: rgba(255, 255, 255, 0.08);
			backdrop-filter: blur(vmin(26.028));

		}
	}

	p.form__text{
		font-family: $MS-Regular;
		font-size: vmin(12);
		color: rgba($color: #fff, $alpha: 0.68);
		padding-top: vmin(5);
		text-align: center;
		line-height: 120%;
	}

	.getpkbtn{
		display: none;
	}
}