.portfolio{

    .portfolio__container{
        margin: 0 auto;

        .services__content{
            position: relative;
            z-index: 10;

            .tab_container{
                margin-top: vw(40);
            }

            .portfolio__header{
                display: grid;
                align-self: center;
                grid-template-columns: vw(600) 1fr;
                position: relative;
                z-index: 15;
            }
        }
    }

}

#portfolio{
    width: vw(600);
    margin: 0;

    &::after{
        width: vw(851);
        left: vw(-246);
        transform: translateX(0);
        bottom: vw(75);
    }

    li{
        &.active{

            &::after{
                width: vw(97);
            }

            &:nth-child(2)::after,
            &:nth-child(3)::after{
                left: 50%;
                transform: translate(-50%);
            }

            &:first-child:after{
                left: 50%;
                transform: translate(-50%);
            }

            &:last-child:after{
                right: vw(-5);
            }
        }

        a{
            span.mob{
                display: none;
            }
        }
    }
}

.portfolio__title{
    width: 100%;
    margin-left: vw(60);

    p.portfolio__header{
        font-family: $DTW-Bold;
        font-size: vw(36);
        text-transform: uppercase;
        background: radial-gradient(50% 50% at 50% 50%, rgba(255, 255, 255, 0.79) 0%, rgba(255, 255, 255, 0.26) 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
    }

    p.portfolio__text{
        color: white;
        font-size: vw(44);
        font-family: $DTW-Bold;
        margin-top: vw(40);
    }
}

.portfolio__images{
    position: absolute;
    width: vw(1039);
    height: vw(1065);
    left: vw(-250);
    top: vw(-368);
    z-index: 5;

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

//============================

.portfolio__slider{
    position: relative;
    height: vw(610);
    z-index: 25;
}

.center{
    position: relative;
}

.slider{
    position: absolute;
    min-width: vw(1667);
    width: 100%;
    overflow: hidden;
    box-shadow: none;

    .slide{
        margin: 0 vw(40);
        box-shadow: none;
    }
}

.slider__buttons{
    display: flex;
    align-items: center;
    position: absolute;
    bottom: vw(35);
    left: vw(533);

    .btn__arrow__stroke{
        width: vw(74);
        height: vw(74);
        position: relative;

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            opacity: 1;
        }
    }

    .slider__arrow--next.slick-disabled,
    .slider__arrow--prev.slick-disabled{

        .btn__arrow__stroke{

            img{
                opacity: .2;
            }
        }
    }

    .btn__arrow{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: vw(60);
        height: vw(60);
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;

        .btn__img{
            width: vw(8);
            height: vw(14);

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

        }

        &::before{
            content: '';
            position: absolute;
            inset: 0;
            border-radius: 50%;
            padding: vw(8);
            background: radial-gradient(50% 50% at 50% 50%, rgba(153, 233, 235, 0) 86.98%, rgba(153, 233, 235, 0.21) 100%);;
            -webkit-mask: 
                linear-gradient(white 0 0) content-box, 
                linear-gradient(white 0 0);
            -webkit-mask-composite: xor;
            mask-composite: exclude;
        }
    }

    .slider__arrow--prev{
        margin-right: vw(30);

        img{
            transform: rotate(-180deg);
        }

        .btn__arrow{

            .btn__img{

                img{
                    transform: rotate(0);
                }
            }
        }
    }

    .slider__navigation__wrapper{
        margin-left: vw(310);
    }

}

.slider__navigation__wrapper{
    .slide__number{

        .pagination{
            font-size: vw(24);
            font-family: $DTW-Medium;
            color: #656565;
            display: flex;
            align-items: flex-start;

            span{
                color: #4D4D4D;
                font-size: vw(16);

                .slash{
                    font-size: vw(16);
                    font-family: $RFDE-Bold;
                }
            }
        }
    }
}

.slider__item{
    width: fit-content;
    height: fit-content;
    background-color: #1AC7FD;
    border-radius: vw(14);
    box-shadow: 0px vw(23) vw(52) rgba(0, 0, 0, 0.21);
    position: relative;
    z-index: 30;

    &::before{
        content: '';
        position: absolute;
        width: vw(3);
        height: calc(100% - vw(13));
        background-color: rgba(#fff, 0.24);
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        border-radius: vw(14) 0 0 vw(14);
    }


    .slider__item__background{
        width: vw(403);
        height: vw(455);

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: vw(14);
        }
    }

    &.slick-current{
        .slider__item__background{
            height: vw(603);
            width: vw(457);
            /* animation: slide-height 1s; */
        }
    }

    .slider__item__top{
        width: vw(258);
        height: vw(110);
        border-radius: 0 vw(14) vw(14) 0;
        position: absolute;
        top: vw(50);
        left: 0;
        z-index: 35;
        padding-left: vw(30);
        background: rgba(36, 58, 58, 0.35);
        backdrop-filter: blur(vw(34));
        display: flex;
        flex-direction: column;
        justify-content: center;

        &::after{
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            height: 100%;
            width: vw(2);
            background-color: #fff;
        }

        &::before{
            content: '';
            position: absolute;
            inset: 0;
            border-radius: 0 vw(14) vw(14) 0;
            padding: vw(2);
            background: linear-gradient(179.22deg, rgba(175, 253, 255, 0) 0.7%, #AFFDFF 52.82%, rgba(175, 253, 255, 0) 100.77%),
            linear-gradient(89.92deg, rgba(175, 253, 255, 0) -1.09%, #AFFDFF 51.52%, rgba(175, 253, 255, 0) 99.92%);
            -webkit-mask: 
                linear-gradient(white 0 0) content-box, 
                linear-gradient(white 0 0);
            -webkit-mask-composite: xor;
            mask-composite: exclude;
        }
        
        
        p.title{
            color: white;
            font-size: vw(24);
            font-family: $DTW-Bold;
            line-height: 120%;
            position: relative;
            z-index: 40;
        }

        p.text{
            color: white;
            letter-spacing: 108%;
            font-size: vw(16);
            font-family: $MS-Regular;
            position: relative;
            z-index: 40;
            margin-top: vw(5);
        }
    }

    .slider__item__bottom{
        position: absolute;
        bottom: vw(50);
        left: vw(50);
        width: vw(282);
        height: vw(56);
        display: flex;
        align-items: center;
        justify-content: center;
        background: rgba(28, 99, 116, 0.7);
        border: 1px solid rgba(255, 255, 255, 0.4);
        backdrop-filter: blur(34px);
        border-radius: 63px;
        z-index: 35;

        p{
            font-size: vw(18);
            font-family: $MS-Regular;
            color: white;
            position: relative;
            z-index: 40;
        }
    }
}

