ul.slick-dots{
    width: vw(360);
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    left: calc(vw(533) + vw(310) + vw(210));
    bottom: vw(57);
    z-index: 50;

    li{
        width: 33.33%;
        height: vw(2);
        background-color: rgba(255, 255, 255, 0.2);
        cursor: pointer;

        button{
            display: none;
        }

        &.slick-active{
            background-color: rgba(255, 255, 255, 0.5);
        }
    }
}

@keyframes slide-height{
    0%{
        height: vw(455);
        width: vw(403);
        opacity: 0;
    }
    25%{
        opacity: .3;
    }
    75%{
        height: vw(603);
        width: vw(457);
        opacity: 1;
    }
}

