.modal {
  --transition-time: 0.3s;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
  cursor: pointer;
  overflow-y: auto;
  overflow-x: hidden;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--transition-time), visibility var(--transition-time);
}

.modal__wrapp {
  display: none;
  cursor: default;
  width: fit-content;
  height: fit-content;
}

.modal.is-open {
  opacity: 1;
  visibility: visible;
  transition: opacity var(--transition-time), visibility var(--transition-time);
}

.modal__wrapp.modal-open {
  display: flex;
}

.disable-scroll {
  position: relative;
  overflow: hidden;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
}

.fade {
  opacity: 0;
  transition: opacity var(--transition-time);
}

.fade.animate-open {
  opacity: 1;
  transition: opacity var(--transition-time);
}

.fadeInUp {
  opacity: 0;
  transform: translateY(-5.2083333333vw);
  transition: opacity var(--transition-time), transform var(--transition-time);
}

.fadeInUp.animate-open {
  opacity: 1;
  transform: translateY(0);
  transition: opacity var(--transition-time), transform var(--transition-time);
}

.modal__content {
  position: absolute;
  bottom: 2.6041666667vw;
  right: 4.4270833333vw;
}
.modal__content .order__call {
  z-index: 9999;
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(3.5208333333vw);
}

p.form__text {
  font-family: "Montserrat-Regular";
  font-size: 0.8333333333vw;
  color: rgba(255, 255, 255, 0.68);
  padding-top: 0.5208333333vw;
  text-align: center;
  line-height: 120%;
}

.modal__close {
  display: flex;
  width: 7.0093457944vw;
  height: 7.0093457944vw;
  border-radius: 50%;
  background-color: #00DCE1;
  position: absolute;
  right: 9.3457943925vw;
  bottom: 96.9626168224vw;
  z-index: 99999;
  border: 0.4672897196vw #AFFDFF solid;
  justify-content: center;
  align-items: center;
}
.modal__close span {
  position: relative;
  width: 7.0093457944vw;
  height: 7.0093457944vw;
  border-radius: 50%;
}
.modal__close span::after {
  content: "";
  position: absolute;
  top: 3.0373831776vw;
  left: 1.285046729vw;
  transform: rotate(45deg);
  width: 3.5046728972vw;
  height: 0.4672897196vw;
  background-color: #AFFDFF;
  border-radius: 1.1682242991vw;
}
.modal__close span::before {
  content: "";
  position: absolute;
  top: 3.0373831776vw;
  left: 1.285046729vw;
  transform: rotate(135deg);
  width: 3.5046728972vw;
  height: 0.4672897196vw;
  border-radius: 1.1682242991vw;
  background-color: #AFFDFF;
}

#quiz__modal .modal__content {
  bottom: auto;
  top: 50%;
  right: auto;
  left: 50%;
